















































































































































































































































































































import {
  computed, defineComponent, onMounted, onUnmounted, PropType, Ref, ref,
} from '@vue/composition-api';
import {
  actionLinkStyles,
  generalStyles,
  iconStyles, logoStyles,
  StyleKeyValue, textStyles,
  tileStyleSelector,
} from '@/app/website/client/logic/tile-style';
import { useWindowScrollSettings } from '@/app/shared/client/logic/scroll-settings';
import { useTileOffsetSettings } from '@/app/shared/client/logic/offset-settings';
import { useCartSettings } from '@/app/shared/client/logic/cart-settings';
import {
  BackgroundType,
  GeneralDesignSettings,
} from '@/app/shared/client/type/settings/general-settings';
import { checkIfTileShown } from '@/app/website/client/logic/appearance-behaviour';
import {
  ViewportSettings,
} from '@/app/shared/client/type/settings/viewport-settings';
import LeftDefaults from '@/app/website/client/defaults/header/left.json';
import {
  HeaderTileContent,
  HeaderTileDesign,
  HeaderTileExternalContent,
  HeaderTileLayout,
} from '@/app/shared/client/type/tile/header-tile';
import { HeaderTileLayoutDefaults } from '@/app/shared/client/type/defaults/header-tile-defaults';
import { ActionLinkSettings } from '@/app/shared/client/type/settings/action-link-settings';
import { isNotNullOrEmpty } from '@/app/shared/client/helper/base';
import PhoneIcon from '@/app/website/client/view/icon/PhoneIcon.vue';
import EmailIcon from '@/app/website/client/view/icon/EmailIcon.vue';
import CartIcon from '@/app/website/client/view/icon/CartIcon.vue';
import SearchIcon from '@/app/website/client/view/icon/SearchIcon.vue';
import ActionLink from '@/app/website/client/view/component/ActionLink.vue';
import { IconSettings } from '@/app/shared/client/type/settings/icon-settings';
import { TextSettings } from '@/app/shared/client/type/settings/text-settings';
import { MutationType } from '@/app/website/client/store/mutation/type';
import { Store } from 'vuex';
import { State } from '@/app/website/client/store/state';
import { LogoType } from '@/app/shared/client/type/logo';
import { getImageSet } from '@/app/website/client/logic/image-style';
import {
  LogoSettings,
  FrameSettings,
  CapitalizationType,
} from '@/app/shared/client/type/settings/logo-settings';
import {
  getImageInUploadingState,
} from '@/app/website/client/logic/image-uploading';
import FontLoader from '@/app/website/client/view/component/FontLoader.vue';
import { sendTrackingSearchEvent } from '@/app/website/client/logic/tracking';
import { getLinkTarget } from '@/app/website/client/helper';
import useI18N from '@/app/shared/client/logic/i18n';
import { useMenuElement } from '@/app/website/client/logic/element/menu';
import {
  SetBurgerMenuOpenStateMutation,
} from '@/app/website/client/store/mutation/set-burger-menu-open-state';
import { debounce } from '@/app/shared/client/helper/hof';

interface HeaderTileProps {
  tileId: string;
  content: HeaderTileContent;
  externalContent: HeaderTileExternalContent;
  design: HeaderTileDesign;
  viewportSettings: ViewportSettings;
}

function getDefaults(layout: HeaderTileLayout | undefined): HeaderTileLayoutDefaults | undefined {
  switch (layout) {
    case 'LEFT':
      return LeftDefaults as HeaderTileLayoutDefaults;
    default:
      return undefined;
  }
}

function getDesignSettings(
  userDesign: HeaderTileDesign,
  defaults?: HeaderTileLayoutDefaults,
): HeaderTileDesign {
  const general: GeneralDesignSettings = {
    ...defaults?.general,
    ...userDesign.general,
    background: {
      ...defaults?.general?.background,
      ...userDesign.general?.background,
      solid: {
        ...defaults?.general?.background?.solid,
        ...userDesign.general?.background?.solid,
      },
      gradient: {
        ...defaults?.general?.background?.gradient,
        ...userDesign.general?.background?.gradient,
      },
    },
  };
  const logo: LogoSettings = {
    ...defaults?.logo as LogoSettings,
    ...userDesign.logo,
    frame: {
      ...defaults?.logo?.frame as FrameSettings,
      ...userDesign.logo?.frame,
    },
  };
  const menu: TextSettings = {
    ...defaults?.menu as TextSettings,
    ...userDesign.menu,
  };
  const phone: TextSettings = {
    ...defaults?.phone as TextSettings,
    ...userDesign.phone,
  };
  const email: TextSettings = {
    ...defaults?.email as TextSettings,
    ...userDesign.email,
  };
  const search: ActionLinkSettings = {
    ...defaults?.search as IconSettings,
    ...userDesign.search,
  };
  const cart: ActionLinkSettings = {
    ...defaults?.cart as IconSettings,
    ...userDesign.cart,
  };
  const actionLink: ActionLinkSettings = {
    ...defaults?.actionLink as ActionLinkSettings,
    ...userDesign.actionLink,
  };

  return {
    layout: userDesign.layout,
    general,
    logo,
    menu,
    phone,
    email,
    search,
    cart,
    actionLink,
  };
}

function computedLayoutClass(layout: HeaderTileLayout | undefined): string {
  switch (layout) {
    case 'LEFT':
      return 'left';
    default:
      return 'left';
  }
}

function useOpenSearchPanelHandler(
  nextTick: () => Promise<void>,
  searchInputRef: Ref<HTMLInputElement | undefined>,
  isSearchPanelOpened: Ref<boolean>,
): () => void {
  function openSearchPanel(): void {
    isSearchPanelOpened.value = true;

    nextTick().then(() => {
      if (searchInputRef.value === undefined) {
        return;
      }

      searchInputRef.value.focus();
    });
  }

  return openSearchPanel;
}

function useCloseSearchPanelHandler(isSearchPanelOpened: Ref<boolean>): () => void {
  function closeSearchPanel(): void {
    isSearchPanelOpened.value = false;
  }

  return closeSearchPanel;
}

function useOpenSearchPageHandler(
  linkTarget: Ref<Readonly<string>>,
  searchInputRef: Ref<HTMLInputElement | undefined>,
): () => void {
  function openSearchPage(): void {
    if (searchInputRef.value === undefined) {
      return;
    }

    const searchInput = searchInputRef.value;
    const keyword = searchInput.value;

    sendTrackingSearchEvent(keyword);

    const url = `/products/search?keyword=${keyword}`;
    window.open(url, linkTarget.value);
  }

  return openSearchPage;
}

function useToggleMenuHandler(
  headerRef: Ref<HTMLElement | undefined> | undefined,
  store: Store<State>,
  isBurgerMenuOpened: Ref<Readonly<boolean>>,
): () => void {
  function toggleMenu(): void {
    store.commit<SetBurgerMenuOpenStateMutation>({
      type: MutationType.SET_BURGER_MENU_OPEN_STATE,
      payload: {
        burgerMenuOpened: !isBurgerMenuOpened.value,
      },
    });
    window.scrollTo({
      top: 0,
    });

    if (headerRef?.value !== undefined) {
      headerRef.value.scrollTo({
        top: 0,
      });
    }
  }

  return toggleMenu;
}

function tileStyles(
  design: HeaderTileDesign,
  logoType: LogoType | undefined,
): StyleKeyValue[] {
  let styles: StyleKeyValue[] = [
    ...generalStyles(design.general),
    ...textStyles('menu', design.menu),
    ...textStyles('phone', design.phone),
    ...textStyles('email', design.email),
    ...iconStyles('search', design.search),
    ...iconStyles('cart', design.cart),
    ...actionLinkStyles('button', design.actionLink),
  ];

  if (logoType === LogoType.TEXT) {
    styles = [
      ...styles,
      ...logoStyles('logo', design.logo),
    ];
  }

  return styles;
}

interface MenuOffsetSettings {
  menuElementContainerOffsetLeft: Ref<number>;
  menuElementWrapOffsetWidth: Ref<number>;
  menuElementWrapScrollLeft: Ref<number>;
  menuElementWrapScrollWidth: Ref<number>;
  subscribeOnMenuOffsetChange: () => void;
}

function useMenuOffsetSettings(
  menuElementContainerRef: Ref<HTMLElement | null | undefined> | undefined,
  menuElementWrapRef?: Ref<HTMLElement | null | undefined> | undefined,
): MenuOffsetSettings {
  const menuElementContainerOffsetLeft = ref(0);
  const menuElementWrapOffsetWidth = ref(0);
  const menuElementWrapScrollLeft = ref(0);
  const menuElementWrapScrollWidth = ref(0);

  function updateOffset(): void {
    menuElementContainerOffsetLeft.value = menuElementContainerRef?.value?.offsetLeft ?? 0;
    menuElementWrapScrollWidth.value = menuElementWrapRef?.value?.scrollWidth ?? 0;
    menuElementWrapOffsetWidth.value = menuElementWrapRef?.value?.offsetWidth ?? 0;
  }

  function updateScroll(): void {
    menuElementWrapScrollLeft.value = menuElementWrapRef?.value?.scrollLeft ?? 0;
  }

  function subscribeOnMenuOffsetChange(): void {
    const debounceUpdateOffset = debounce(updateOffset, 100);
    window.addEventListener('resize', debounceUpdateOffset);
    updateOffset();

    menuElementWrapRef?.value?.addEventListener('scroll', updateScroll);
    menuElementWrapRef?.value?.addEventListener('wheel', (event) => {
      const ev = event as WheelEvent;
      if (menuElementWrapRef && menuElementWrapRef.value
        && ev.deltaY
        && menuElementWrapScrollWidth.value > menuElementWrapOffsetWidth.value) {
        ev.preventDefault();
        ev.stopPropagation();
        menuElementWrapRef.value.scrollLeft += ev.deltaY;
      }
    });
  }

  return {
    menuElementContainerOffsetLeft,
    menuElementWrapOffsetWidth,
    menuElementWrapScrollLeft,
    menuElementWrapScrollWidth,
    subscribeOnMenuOffsetChange,
  };
}

const HeaderTile = defineComponent({
  components: {
    PhoneIcon,
    EmailIcon,
    CartIcon,
    SearchIcon,
    FontLoader,
    ActionLink,
  },
  props: {
    tileId: {
      required: true,
      type: String,
    },
    content: {
      required: true,
      type: Object as PropType<HeaderTileContent>,
    },
    externalContent: {
      required: true,
      type: Object as PropType<HeaderTileExternalContent>,
    },
    design: {
      required: true,
      type: Object as PropType<HeaderTileDesign>,
    },
    viewportSettings: {
      required: true,
      type: Object as PropType<ViewportSettings>,
    },
  },
  setup(props: HeaderTileProps, { attrs, root, emit }) {
    const i18n = useI18N(root.$store);

    const content = computed(() => props.content);
    const design = computed(() => {
      const defaults = getDefaults(props.design.layout);
      return getDesignSettings(props.design, defaults);
    });

    const tileRef = ref<HTMLElement>();
    const headerRef = ref<HTMLElement>();
    const state = root.$store.state as State;
    const profile = state.profile;
    const storeName = profile.storeName;

    const {
      windowScrollTop,
      subscribeOnWindowScrollChange,
    } = useWindowScrollSettings();

    const {
      tileOffsetTop,
      tileOffsetHeight,
      subscribeOnTileOffsetChange,
    } = useTileOffsetSettings(tileRef);

    const {
      cartItemCount,
      cartNeedBounce,
      subscribeOnCartChange,
    } = useCartSettings(profile.siteId);

    const layoutClass = computed(() => `ins-tile--${computedLayoutClass(design.value.layout)}`);

    const isTileShowRef = ref(false);
    const isTileShown = computed(() => checkIfTileShown(
      isTileShowRef,
      props.viewportSettings,
      windowScrollTop.value,
      tileOffsetTop.value,
      tileOffsetHeight.value,
    ));

    const fonts = computed(() => [
      design.value.logo?.font,
      design.value.menu?.font,
      design.value.phone?.font,
      design.value.email?.font,
      design.value.actionLink?.font,
    ]);

    const styleSelector = computed(() => {
      const styles = tileStyles(design.value, content.value.logo.type);
      return tileStyleSelector(`#${attrs.id}`, styles);
    });

    const isPreviewMode = computed(() => root.$store.getters.isPreviewMode);
    const linkTarget = computed(() => getLinkTarget(isPreviewMode.value));

    const isBurgerMenuOpened = computed(() => root.$store.getters.isBurgerMenuOpened);
    const toggleMenu = useToggleMenuHandler(headerRef, root.$store, isBurgerMenuOpened);

    const isTextLogo = computed(() => content.value.logo.type === LogoType.TEXT);
    const isImageLogo = computed(() => content.value.logo.type === LogoType.IMAGE);
    const hasLogo = computed(() => {
      const isLogoVisible = design.value.logo?.visible;

      if (isTextLogo.value) {
        return isLogoVisible && isNotNullOrEmpty(content.value.logo.text);
      }

      if (isImageLogo.value) {
        return isLogoVisible && content.value.logo.image !== undefined;
      }

      return false;
    });
    const hasLogoFrame = computed(() => {
      const isLogoFrameVisible = design.value.logo?.frame?.visible;
      const isLogoFrameTransparent = (design.value.logo?.frame?.color?.hsl?.a ?? 1) === 0;
      return isLogoFrameVisible && !isLogoFrameTransparent;
    });
    const logoText = computed(() => content.value.logo.text);
    const logoCapitalizationType = computed(() => design.value.logo?.capitalization);

    const uploadingLogoImage = computed(() => {
      const { imageListInUploadingState } = root.$store.state as State;
      return getImageInUploadingState(imageListInUploadingState, props.tileId, 'logo-image');
    });
    const isUploadingLogoImage = computed(() => uploadingLogoImage.value !== undefined);
    const logoImage = computed(() => {
      const imageSet = getImageSet(
        root.$store.state.context,
        uploadingLogoImage.value,
        content.value.logo.image,
      );

      return imageSet?.url600;
    });

    const storeCategories = computed(() => props.externalContent.storeData.categories);
    const menuContent = computed(() => content.value.menu);
    const menuDesign = computed(() => design.value.menu);
    const menuElement = useMenuElement(
      root.$store,
      storeCategories,
      menuContent,
      menuDesign,
    );

    const hasPhone = computed(() => {
      const isPhoneVisible = design.value.phone?.visible;
      const hasPhoneInfo = isNotNullOrEmpty(content.value.contactInfo?.phone);
      return isPhoneVisible && hasPhoneInfo;
    });

    const hasEmail = computed(() => {
      const isEmailVisible = design.value.email?.visible;
      const hasEmailInfo = isNotNullOrEmpty(content.value.contactInfo?.email);
      return isEmailVisible && hasEmailInfo;
    });

    const hasSearch = computed(() => design.value.search?.visible);
    const searchInputRef = ref<HTMLInputElement>();
    const isSearchPanelOpened = ref(false);
    const openSearchPanel = useOpenSearchPanelHandler(
      root.$nextTick,
      searchInputRef,
      isSearchPanelOpened,
    );
    const openSearchPage = useOpenSearchPageHandler(
      linkTarget,
      searchInputRef,
    );
    const closeSearchPanel = useCloseSearchPanelHandler(isSearchPanelOpened);

    const hasCart = computed(() => design.value.cart?.visible);

    const actionLinkContent = computed(() => content.value.actionLink);
    const actionLinkDesign = computed(() => design.value.actionLink);

    const menuElementContainerRef = ref<HTMLElement>();
    const menuElementWrapRef = ref<HTMLElement>();
    const {
      menuElementContainerOffsetLeft,
      menuElementWrapOffsetWidth,
      menuElementWrapScrollLeft,
      menuElementWrapScrollWidth,
      subscribeOnMenuOffsetChange,
    } = useMenuOffsetSettings(menuElementContainerRef, menuElementWrapRef);

    const hoveredMenuItemStyles = computed(() => {
      const position = menuElement.storeItemPositionCenter
        - menuElementWrapScrollLeft.value
        + menuElementContainerOffsetLeft.value;
      return { left: `${position}px` };
    });

    const scrollerMarkerStyles = computed(() => ({
      left: `${(menuElementWrapScrollLeft.value / menuElementWrapScrollWidth.value) * 100}%`,
      width: `${(menuElementWrapOffsetWidth.value / menuElementWrapScrollWidth.value) * 100}%`,
    }));

    const isLargeMenu = computed(
      () => menuElementWrapOffsetWidth.value < menuElementWrapScrollWidth.value,
    );

    const isGradientBackground = computed(
      () => design.value.general?.background?.type === BackgroundType.GRADIENT,
    );

    onMounted(() => {
      emit('tile-loaded');
      subscribeOnWindowScrollChange();
      subscribeOnTileOffsetChange();
      subscribeOnMenuOffsetChange();
      subscribeOnCartChange();
    });

    onUnmounted(() => {
      emit('tile-unloaded');
    });

    return {
      i18n,
      tileRef,
      headerRef,
      layoutClass,
      fonts,
      styleSelector,
      isPreviewMode,
      linkTarget,
      isBurgerMenuOpened,
      toggleMenu,
      isTextLogo,
      isImageLogo,
      hasLogo,
      hasLogoFrame,
      logoText,
      logoCapitalizationType,
      CapitalizationType,
      logoImage,
      isUploadingLogoImage,
      storeCategories,
      menuElement,
      hasPhone,
      hasEmail,
      hasSearch,
      searchInputRef,
      isSearchPanelOpened,
      openSearchPanel,
      closeSearchPanel,
      openSearchPage,
      hasCart,
      isTileShown,
      actionLinkContent,
      actionLinkDesign,
      menuElementContainerRef,
      menuElementWrapRef,
      hoveredMenuItemStyles,
      scrollerMarkerStyles,
      isLargeMenu,
      isGradientBackground,
      cartItemCount,
      cartNeedBounce,
      storeName,
    };
  },
});

export default HeaderTile;
