import { ref, Ref } from '@vue/composition-api';
import { debounce } from '@/app/shared/client/helper/hof';
import Ecommerce from '@ecwid/sdk';

interface CartSettings {
  cartItemCount: Ref<number>;
  cartNeedBounce: Ref<boolean>;
  subscribeOnCartChange: () => void;
}

interface CartProps {
  productsQuantity?: number,
}

export function useCartSettings(
  storeId: number | undefined,
): CartSettings {
  const cartItemCount = ref(0);
  const cartNeedBounce = ref(false);
  const ecommerce = new Ecommerce({
    storeId: storeId ?? 0,
  });

  function prepareAnimation():void {
    cartNeedBounce.value = false;
  }

  function runAnimation(): void {
    cartNeedBounce.value = true;
  }

  const debounceRunAnimation = debounce(runAnimation, 50);

  function setCartItemCount(): void {
    ecommerce.cart.get().then((result: CartProps) => {
      cartItemCount.value = result?.productsQuantity ?? 0;
    });
  }

  function updateCartItemCount(): void {
    ecommerce.cart.get().then((result: CartProps) => {
      const cartUpdatedCount = result?.productsQuantity ?? 0;
      if (cartItemCount.value < cartUpdatedCount) {
        prepareAnimation();
        debounceRunAnimation();
      }
      cartItemCount.value = cartUpdatedCount;
    });
  }

  const debounceUpdateCartItemCount = debounce(updateCartItemCount, 100);

  function subscribeOnCartChange(): void {
    if (typeof document === 'undefined') {
      return;
    }

    window.addEventListener('load', () => {
      if (window.Ecwid) {
        window.Ecwid.OnCartChanged.add(debounceUpdateCartItemCount);
      }
      setCartItemCount();
    });

    document.addEventListener('visibilitychange', () => {
      if (document.visibilityState !== 'hidden') {
        setCartItemCount();
      }
    }, false);

    setCartItemCount();
  }

  return {
    cartItemCount,
    cartNeedBounce,
    subscribeOnCartChange,
  };
}

export default {};
