import {
  computed, reactive, ref, Ref, UnwrapRef,
} from '@vue/composition-api';
import { MenuData, MenuItemData, MenuItemType } from '@/app/shared/client/type/menu';
import { TextSettings } from '@/app/shared/client/type/settings/text-settings';
import { CategoryListComponentItem } from '@/app/shared/client/type/component/category-list-component';
import { isNullOrEmpty } from '@/app/shared/client/helper/base';
import { useScrollPosition } from '@/app/website/client/logic/scroll-position';
import { Store } from 'vuex';
import { State } from '@/app/website/client/store/state';

interface MenuItemElement {
  readonly id: string;
  readonly title: string | undefined;
  readonly type: string | undefined;
  readonly link: string | undefined;
  readonly email: string | undefined;
  readonly phone: string | undefined;
  readonly tileIdForScroll: string | undefined;
  readonly showStoreCategories: boolean;
  readonly showStoreLinks: boolean;
  readonly linkHref: string | undefined;
  readonly hasStoreSubmenu: boolean;
  readonly performCustomAction: () => void;
}

interface MenuElement {
  readonly hasElement: Ref<Readonly<boolean>>;
  readonly itemList: Ref<Readonly<MenuItemElement[]>>;
  readonly hoveredMenuItem: Ref<Readonly<MenuItemElement> | null>;
  readonly storeItemPositionCenter: number;
  readonly setHoveredMenuItem: (event: Event, menuItem: MenuItemElement | null) => void;
}

export function hasMenuElement(
  itemList: Ref<Readonly<MenuItemElement[]>>,
  design: Ref<Readonly<TextSettings | undefined>>,
): boolean {
  const isVisible = design.value?.visible ?? false;
  const hasItemList = itemList.value.length > 0;
  return isVisible && hasItemList;
}

function menuItemLinkHref(menuItem: Readonly<MenuItemData>): string | undefined {
  switch (menuItem.type) {
    case MenuItemType.HYPER_LINK: return menuItem.link ?? '/';
    case MenuItemType.MAIL_LINK: return `mailto:${menuItem.email}`;
    case MenuItemType.TEL_LINK: return `tel:${menuItem.phone}`;
    case MenuItemType.SCROLL_TO_TILE: return undefined;
    case MenuItemType.GO_TO_STORE: return '/products';
    default: throw new Error('Unknown MenuItemType: ');
  }
}

function menuItemStoreCategoriesShown(
  menuItem: Readonly<MenuItemData>,
  storeCategories: Readonly<CategoryListComponentItem[]>,
): boolean {
  const showStoreCategories = menuItem.showStoreCategories ?? true;
  return showStoreCategories && storeCategories.length > 0;
}

function menuItemHasStoreSubmenu(
  menuItem: Readonly<MenuItemData>,
  storeCategories: Readonly<CategoryListComponentItem[]>,
): boolean {
  if (menuItem.type !== MenuItemType.GO_TO_STORE) {
    return false;
  }

  const showStoreCategories = menuItemStoreCategoriesShown(menuItem, storeCategories);
  const showStoreLinks = menuItem.showStoreLinks ?? true;
  return showStoreCategories || showStoreLinks;
}

function createMenuItemCustomActionHandler(
  store: Store<State>,
  menuItem: Readonly<MenuItemData>,
): () => void {
  function performMenuItemCustomAction(): void {
    if (menuItem.type !== MenuItemType.SCROLL_TO_TILE) {
      return;
    }

    const scrollPosition = useScrollPosition(store);
    scrollPosition.scrollToTile(menuItem.tileIdForScroll, true);
  }

  return performMenuItemCustomAction;
}

function createMenuItemElement(
  store: Store<State>,
  menuItem: Readonly<MenuItemData>,
  storeCategories: Readonly<CategoryListComponentItem[]>,
): MenuItemElement {
  return {
    id: menuItem.id,
    title: menuItem.title,
    type: menuItem.type,
    link: menuItem.link,
    email: menuItem.email,
    phone: menuItem.phone,
    tileIdForScroll: menuItem.tileIdForScroll,
    showStoreCategories: menuItemStoreCategoriesShown(menuItem, storeCategories),
    showStoreLinks: menuItem.showStoreLinks ?? true,
    linkHref: menuItemLinkHref(menuItem),
    hasStoreSubmenu: menuItemHasStoreSubmenu(menuItem, storeCategories),
    performCustomAction: createMenuItemCustomActionHandler(store, menuItem),
  };
}

export function useMenuElement(
  store: Store<State>,
  storeCategories: Ref<Readonly<CategoryListComponentItem[]>>,
  content: Ref<Readonly<MenuData | undefined>>,
  design: Ref<Readonly<TextSettings | undefined>>,
): UnwrapRef<MenuElement> {
  const itemList = computed(() => {
    const items = content.value?.items ?? [];
    return items
      .filter((item) => !isNullOrEmpty(item.title))
      .map((item) => createMenuItemElement(store, item, storeCategories.value));
  });
  const hasElement = computed(() => hasMenuElement(itemList, design));

  const hoveredMenuItem: Ref<MenuItemElement | null> = ref(null);
  const storeItemPositionCenter = ref(0);
  const setHoveredMenuItem = (
    event: Event,
    menuItem: MenuItemElement | null,
  ): void => {
    if (menuItem && menuItem.hasStoreSubmenu) {
      hoveredMenuItem.value = menuItem;
      const el = event.target as HTMLElement;
      storeItemPositionCenter.value = el.offsetLeft + el.offsetWidth / 2;
    } else {
      hoveredMenuItem.value = null;
    }
  };

  return reactive({
    hasElement,
    itemList,
    hoveredMenuItem,
    storeItemPositionCenter,
    setHoveredMenuItem,
  });
}

export default {};
